const Profile = ({ changeFilter }) => {
  return (
    <div className="profile-wrap">
      <div className="profile">
        <img src="/images/image-jeremy.png" alt="" />
        <p>
          <span>Report for</span>
          <br />
          Jeremy Robson
        </p>
      </div>
      <div className="filter">
        <p className="filters" onClick={(e) => changeFilter('daily', e.target)}>
          Daily
        </p>
        <p
          className="filters selected"
          onClick={(e) => changeFilter('weekly', e.target)}>
          Weekly
        </p>
        <p
          className="filters"
          onClick={(e) => changeFilter('monthly', e.target)}>
          Monthly
        </p>
      </div>
    </div>
  );
};

export default Profile;
