import { useState } from 'react';
import Cards from './components/Cards';

import Profile from './components/Profile';

const App = () => {
  const [filter, setfilter] = useState('weekly');
  const changeFilter = (filter, value) => {
    setfilter(filter);
    document.querySelectorAll('.filters').forEach((c) => {
      c.className = 'filters';
    });
    value.className = 'filters selected';
    console.log(value);
  };

  return (
    <div className="wrap">
      <Profile changeFilter={changeFilter} />
      <Cards filter={filter} />
    </div>
  );
};

export default App;
