const Card = ({ title, current, previous, last }) => {
  return (
    <div className={`card-wrap ${title.replace(/\s+/g, '')}`}>
      <img
        className="bgimg"
        src={`./images/icon-${title.toLowerCase()}.svg`}
        alt="lol"
      />
      <div className="card">
        <div className="title">
          <p>{title}</p>
          <img src="./images/icon-ellipsis.svg" alt="title" />
        </div>
        <div className="data">
          <p className="current">{current}hrs</p>
          <p className="previous">
            {last} - {previous}hrs
          </p>
        </div>
      </div>
    </div>
  );
};

export default Card;
