import data from '../data.json';
import Card from './Card';

const Cards = (filter) => {
  const ff = filter.filter;
  return (
    <>
      {ff === 'daily'
        ? data.map((d) => {
            return (
              <Card
                key={d.title}
                title={d.title}
                current={d.timeframes.daily.current}
                previous={d.timeframes.daily.previous}
                last="Yesterday"
              />
            );
          })
        : null}
      {ff === 'weekly'
        ? data.map((d) => {
            return (
              <Card
                key={d.title}
                title={d.title}
                current={d.timeframes.weekly.current}
                previous={d.timeframes.weekly.previous}
                last="Last Week"
              />
            );
          })
        : null}
      {ff === 'monthly'
        ? data.map((d) => {
            return (
              <Card
                key={d.title}
                title={d.title}
                current={d.timeframes.monthly.current}
                previous={d.timeframes.monthly.previous}
                last="Last Month"
              />
            );
          })
        : null}
    </>
  );
};

export default Cards;
